.page__section {

    width: 100%
}

.page__header {

    height: 5vh
}

.page__main {

    overflow: hidden;

    --tw-perspective: 1000px;

    perspective: var(--tw-perspective);

    height: 90vh
}

.page__footer {

    display: flex;

    height: 5vh;

    align-items: center;

    justify-content: center
}

.main__layersContainer {

    height: 100%;

    transition-property: perspective, translate, scale, transform, perspective, rotate;

    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);

    transition-duration: 150ms;

    will-change: transform;

    transform-style: preserve-3d
}

.main__layer {

    position: absolute;

    --tw-rotate-x: var(--movePos-x);

    --tw-rotate-y: var(--movePos-y);

    --webkit-transform: rotateX(var(--tw-rotate-x)) rotateY(var(--tw-rotate-y)) skewX(var(--tw-skew-x)) skewY(var(--tw-skew-y));

    transform: rotateX(var(--tw-rotate-x)) rotateY(var(--tw-rotate-y)) skewX(var(--tw-skew-x)) skewY(var(--tw-skew-y))
}

.layer__wallpaper {

    inset: -9rem;

    background-image: url('src/assets/pictures/city.wallapaper.jpg');

    background-size: cover;

    background-position: center;

    --tw-translate-z: 0px;

    translate: var(--tw-translate-x) var(--tw-translate-y) var(--tw-translate-z);

    --tw-scale-x: 0.5;

    --tw-scale-y: 0.5;

    --tw-scale-z: 0.5;

    scale: var(--tw-scale-x) var(--tw-scale-y) var(--tw-scale-z)
}

.layer__rain {

    --tw-translate-z: 1.25rem;

    translate: var(--tw-translate-x) var(--tw-translate-y) var(--tw-translate-z);

    --tw-scale-x: 1;

    --tw-scale-y: 1;

    --tw-scale-z: 1;

    scale: var(--tw-scale-x) var(--tw-scale-y) var(--tw-scale-z)
}

.layer__logotype {

    inset: 0px;

    display: flex;

    align-items: center;

    justify-content: center;

    background-size: cover;

    background-position: center;

    --tw-translate-z: 1.25rem;

    translate: var(--tw-translate-x) var(--tw-translate-y) var(--tw-translate-z);

    --tw-rotate-y: 2deg;

    --webkit-transform: rotateX(var(--tw-rotate-x)) rotateY(var(--tw-rotate-y)) skewX(var(--tw-skew-x)) skewY(var(--tw-skew-y));

    transform: rotateX(var(--tw-rotate-x)) rotateY(var(--tw-rotate-y)) skewX(var(--tw-skew-x)) skewY(var(--tw-skew-y));

    --tw-scale-x: 1;

    --tw-scale-y: 1;

    --tw-scale-z: 1;

    scale: var(--tw-scale-x) var(--tw-scale-y) var(--tw-scale-z)
}

.layer__console {

    inset: 0px;

    display: flex;

    flex-direction: column;

    align-items: center;

    justify-content: center
}

.console {

    margin-top: 16rem;

    display: flex;

    width: 100%;

    max-width: 28rem;

    align-items: flex-start;

    justify-content: space-between;

    gap: 1rem
}

.console > span {

    display: none
}

@media (min-width: 640px) {

    .console > span {

        display: block
    }
}

.console > ul {

    margin-top: 0.125rem;

    width: 100%;

    max-width: 24rem;

    list-style-position: inside;

    list-style-type: disc
}

.console > ul > :not([hidden]) ~ :not([hidden]) {

    --tw-divide-y-reverse: 0;

    border-top-width: calc(2px * calc(1 - var(--tw-divide-y-reverse)));

    border-bottom-width: calc(2px * var(--tw-divide-y-reverse))
}

.console > ul {

    border-bottom-right-radius: calc(var(--radius) - 2px);

    border-bottom-left-radius: calc(var(--radius) - 2px);

    background-color: hsl(var(--card) / 0.5);

    padding-left: 0.5rem;

    padding-right: 0.5rem;

    --tw-backdrop-blur: blur(4px);

    backdrop-filter: var(--tw-backdrop-blur) var(--tw-backdrop-brightness) var(--tw-backdrop-contrast) var(--tw-backdrop-grayscale) var(--tw-backdrop-hue-rotate) var(--tw-backdrop-invert) var(--tw-backdrop-opacity) var(--tw-backdrop-saturate) var(--tw-backdrop-sepia)
}

.console > ul *::marker {

    color: hsl(var(--constructive))
}

.console > ul::marker {

    color: hsl(var(--constructive))
}

.console__progressBar {

    max-width: 28rem
}

.layer__submitBar {

    bottom: 13rem;

    display: flex;

    width: 100%;

    align-items: center;

    justify-content: center;

    gap: 1rem;

    background-position: center;

    --tw-translate-z: 15rem;

    translate: var(--tw-translate-x) var(--tw-translate-y) var(--tw-translate-z);

    --tw-rotate-y: 2deg;

    --webkit-transform: rotateX(var(--tw-rotate-x)) rotateY(var(--tw-rotate-y)) skewX(var(--tw-skew-x)) skewY(var(--tw-skew-y));

    transform: rotateX(var(--tw-rotate-x)) rotateY(var(--tw-rotate-y)) skewX(var(--tw-skew-x)) skewY(var(--tw-skew-y))
}

.layer__submitBar > button:hover {

    background-color: hsl(var(--secondary))
}
